import styled from "styled-components";
import { GiHamburgerMenu } from "react-icons/gi";
import { IoClose } from "react-icons/io5";
import { Link } from "react-scroll";
import logo from "../../img/logo_transparent.png"

export const Container = styled.div`
    width: 100%;
    height: 80px;
    background: #000;

`
export const Menu = styled.div`
    cursor: pointer;
    width: 100%;
    height: 80px;
    padding: 0 10px;

    display: flex;
    justify-content: center;
    align-items: center;

    @media (min-width: 1300px){
        width: 1300px;
        margin: 0 auto;
    }
`

export const IconMenuMobile = styled(GiHamburgerMenu)`
    font-size: 2.5rem;
    color: ${props=> props.theme.secundary_color};

    &:hover{
        color: ${props=> props.theme.ternary_color};
    }

    @media (min-width: 750px){
        display: none;
    }
`

export const LogoIMG = styled.div`
    cursor: pointer;
    width: 80%;
    height: 80px;
    background-image: url(${logo});
    background-size: cover;
    background-size: 200px;
    background-repeat: no-repeat;

    @media (min-width: 750px){
        width: 20%;
        background-size: 140px;
        margin-top: 20px;
    }

    @media (min-width: 950px){
        width: 20%;
        background-size: 200px;
        margin-top: 0px;
    }

`

export const MenuDesktop = styled.div`
    width: 80%;
    height: 80px;
    padding-top: 20px;
    float: left;

    @media (max-width: 750px){
        display: none;
    }


    ul{
        width: 100%;
        list-style: none;
        display: inline-block;
        display: flex;
        justify-content: space-around;
        align-items: center;

        .li-busca{
            width: 40%;
            float: left;
            position: relative;
            input{
                width: 100%;
                height: 40px;
                padding-left: 10px;
                outline: none;
            }

            .icone-search{
                cursor: pointer;
                height: 40px;
                width: 50px;
                position: absolute;
                color:${props=> props.theme.primary_color};
                top: 0px;
                right: 0px;
                background: transparent;
                padding: 10px;

                &:hover{
                    background: ${props=> props.theme.ternary_color};
                    color: ${props=> props.theme.secundary_color};
                }
            }
        }

        .li{
            width: 15%;
            float: left;
            text-align: center;

            .link-menu-desktop{
                display: inline-block;

                &:hover > p{
                    color: ${props=> props.theme.ternary_color};
                }

                &:hover > .icone-menu{
                    color: ${props=> props.theme.ternary_color};
                }

                .icone-menu{
                    font-size: 1.5rem;
                    color: ${props=> props.theme.secundary_color};
                    float: left;
                }
                p{
                    width: 35%;
                    float: right;
                    color: ${props=> props.theme.secundary_color};
                    font-size: .9rem;
                    margin-top: 3px;
                }
            }
        }
    }

`

export const ContainerSub = styled.div`
    display: none;
    width: 100%;
    height: 80px;
    background: ${props=> props.theme.ternary_color};

    @media (min-width: 750px){
        display: block;
    }

`

export const SubMenu = styled.div`
    cursor: pointer;
    width: 100%;
    height: 50px;
    background: ${props=> props.theme.ternary_color};

    @media (min-width: 1300px){
        width: 1300px;
        margin: 0 auto;
    }


    ul{
        width: 100%;
        list-style: none;
        display: flex;
        justify-content: center;
        align-items: center;

        li{
            margin-top: 14px;
            margin-right: 10px;

            .link-submenu-desktop{
                color: ${props=> props.theme.secundary_color};
                
                &:hover{
                    border-bottom: 2px solid #fff;
                    padding-bottom: 11px;
                }
            }

        }
    }
`

export const SideBar = styled.div`
    width: 300px;
    height: 100vh;
    background: ${props=> props.theme.ternary_color};
    position: absolute;
    top: 0px;
    right:0px;

    
    nav{
        width: 100%;
        margin-top: 80px;
        ul{
            list-style: none;
            li{
                width: 100%;
                height: 45px;
               .link-submenu-mobile{
                color: ${props=> props.theme.secundary_color}; 
                text-align: left;
                font-size: 1.5rem;
                padding: 20px ;
               } 
            }
            .li-bottom{
                width: 100%;
                height: 50px;
                position: absolute;
                bottom: 5px;

                display: flex;
                justify-content: space-around;

                .link-button-bottom-mobile{
                    width: 48%;
                    background: ${props=> props.theme.secundary_color}; 

                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    color: ${props=> props.theme.ternary_color};
                
                    .icone-menu{
                        margin-bottom: 5px;
                    }
                }   
            }
        }
    }
`

export const IconFecharMenuMobile = styled(IoClose)`
    cursor: pointer;
    float: right;
    font-size: 2.5rem;
    color: ${props=> props.theme.secundary_color};
    margin: 20px;

    &:hover{
        color: ${props=> props.theme.primary_color};
    }

`