
import React from 'react'
import {
    Container,
    Menu,
    LogoIMG,
    IconMenuMobile,
    MenuDesktop,
    ContainerSub,
    SubMenu,
    SideBar,
    IconFecharMenuMobile,
} from "./NavElements"
import { Link } from 'react-router-dom'
import { FaHeart, FaUserAlt, FaShoppingCart, FaSearch } from "react-icons/fa"

export const Nav = () => {
    return (
        <>
            <Container>
                <Menu>

                    <LogoIMG />
                    <IconMenuMobile />

                    <MenuDesktop>
                        <nav>
                            <ul>
                                <li className="li-busca">
                                    <Link className="link-menu-desktop"><input type="search" name="busca" id="busca" /></Link>
                                    <FaSearch className="icone-search" />
                                </li>

                                <li className="li">
                                    <Link className="link-menu-desktop"><FaHeart className="icone-menu" /><p>Favoritos</p></Link>
                                </li>

                                <li className="li">
                                    <Link className="link-menu-desktop"><FaUserAlt className="icone-menu" /><p>Entrar</p></Link>
                                </li>

                                <li className="li">
                                    <Link className="link-menu-desktop"><FaShoppingCart className="icone-menu" /></Link>
                                </li>
                            </ul>
                        </nav>
                    </MenuDesktop>
                </Menu>

            </Container>

            <ContainerSub style={{ background: "#ff266a", height: "50px" }}>
                <SubMenu>
                    <nav>
                        <ul>
                            <li className="li">
                                <Link className="link-submenu-desktop">Todos</Link>
                            </li>

                            <li className="li">
                                <Link className="link-submenu-desktop">Vestidos</Link>
                            </li>
                            <li className="li">
                                <Link className="link-submenu-desktop">Promoções</Link>
                            </li>

                            <li className="li">
                                <Link className="link-submenu-desktop">Macaquinhos</Link>
                            </li>

                            <li className="li">
                                <Link className="link-submenu-desktop">Blusas</Link>
                            </li>

                            <li className="li">
                                <Link className="link-submenu-desktop">Shorts</Link>
                            </li>

                            <li className="li">
                                <Link className="link-submenu-desktop">Calças</Link>
                            </li>

                            <li className="li">
                                <Link className="link-submenu-desktop">Conjuntos</Link>
                            </li>

                            <li className="li">
                                <Link className="link-submenu-desktop">Plus Size</Link>
                            </li>

                        </ul>
                    </nav>

                </SubMenu>
            </ContainerSub>

            <SideBar>
                <IconFecharMenuMobile />

                <nav>
                    <ul>
                        <li className="li">
                            <Link className="link-menu-desktop"><FaShoppingCart className="icone-menu" /></Link>
                        </li>

                        <li className="li">
                            <Link className="link-submenu-mobile">Todos</Link>
                        </li>

                        <li className="li">
                            <Link className="link-submenu-mobile">Vestidos</Link>
                        </li>
                        <li className="li">
                            <Link className="link-submenu-mobile">Promoções</Link>
                        </li>

                        <li className="li">
                            <Link className="link-submenu-mobile">Macaquinhos</Link>
                        </li>

                        <li className="li">
                            <Link className="link-submenu-mobile">Blusas</Link>
                        </li>

                        <li className="li">
                            <Link className="link-submenu-mobile">Shorts</Link>
                        </li>

                        <li className="li">
                            <Link className="link-submenu-mobile">Calças</Link>
                        </li>

                        <li className="li">
                            <Link className="link-submenu-mobile">Conjuntos</Link>
                        </li>

                        <li className="li">
                            <Link className="link-submenu-mobile">Plus Size</Link>
                        </li>

                        <li className="li li-bottom">
                            <Link className="link-button-bottom-mobile"><FaUserAlt className="icone-menu" /><p>Entrar</p></Link>
                            <Link className="link-button-bottom-mobile link-right"><FaHeart className="icone-menu" /><p>Favoritos</p></Link>
                        </li>

    
                    </ul>
                </nav>

            </SideBar>
        </>
    )
}


